* {
	box-sizing: border-box;
}
body {
	padding: 20px;
}
.flex-grid {
	margin: 0 0 20px 0;
}
.selected {
	padding: 20px;
	background: green;
}
.unselected {
	padding: 20px;
	background: salmon;
}
.flex-grid-center {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	width: 100%;
}

.column-center-side {
	display: flex;
	flex-direction: column;
	height: 300px;
	flex: 1 1;
}

.column-center-middle {
	display: flex;
	flex-direction: column;
	flex-basis: 100%;
	flex: 4 1;
	height: 300px;
}

.blue-column {
	background-color: lightblue;
	height: 100%;
}
.giphy {
	max-height: 300px;
}
.pressyouluck {
	height: 200px;
	width: 200px;
}

